@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,700;0,900;1,300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,700;0,900;1,300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Navbar {
    height: 50px;
    background-color: #2b2b2b;
    font-family: 'Roboto', sans-serif;
}

/* Logo on the left */
.logo {
    display: flex;
    float: left;
}

.logo p {
    font-family: 'Roboto', sans-serif;
    margin: 25px 18px 25px 18px;
    line-height: 0px;
    font-size: 25px;
    font-weight: 900;
}
.logo--red {
    color: red;
}
.logo--sec {
    color: white;
}

/* Buttons on the right */
.navbar-buttons {
    display: flex;
    float: right;
}

.Navbar ul {
    list-style-type: none;
    margin: 0px 15px 0px 0px;
    padding: 0px;
    display: flex;
}

.Navbar li {
    margin: 0px 15px 0px 15px;
    height: 50px;
    display: flex;
}

.Navbar button {
    border: none;
    background: inherit;
    color: rgb(240, 240, 240);
    opacity: 0.9;
    font-size: 15px;
    font-weight: 700;
    font-family: 'Roboto', sans-serif;
}

.Navbar button[data-active=true] {
    color: red;
    opacity: 1;
}

.Navbar button:hover {
    opacity: 1;
}

.Navbar button:active {
    color: rgb(255, 140, 140);
}
.block {
    width: 100%;
}
.block-header {width: 100%}
.block-header p {
    font-size: 30px;
    font-weight: 700;
    font-family: 'Roboto', sans-serif;
    text-align: center;
    line-height: 50px;
    margin: 0px;
}
.block:nth-child(even) {background-color: #383838}
.block:nth-child(odd) {background-color: #F4F4F4}
.block:nth-child(even) .block-header p {
    background-color: #313131;
    color: #FF3636;
}
.block:nth-child(odd) .block-header p {
    background-color: #FFFFFF;
    color: #2B2B2B
}

/* Catchy Block */
.catchy-block {
    background-color: #383838;
    padding: 50px 50px 20px 50px;
}
.catchy-block .block-content > .catchphrase {
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 900;
    font-size: 60px;
    line-height: 63px;
}
.catchphrase p {margin: 0px}
.catchphrase p:nth-child(odd) {color: white}
.catchphrase p:nth-child(even) {color: red}

.catchy-block .block-content > p {
    font-family: 'Roboto', sans-serif;
    color: white;
    font-size: 20px;
    width: 28%;
    margin: 15px 0px 20px 0px;
}
.catchy-block ul {
    list-style: none;
    padding: 0
}
.catchy-block li {
    display: inline-block;
    padding: 0px 30px 0px 0px;
}
.catchy-block li button {
    padding: 0px 20px 0px 20px;
    height: 47px;
    border-radius: 18px;
    background: white;
    color: #383838;
    font-size: 18px;
    line-height: 0px;
    vertical-align: middle;
    font-family: 'Roboto', sans-serif;
    font-weight: bold;
    outline: none;
    border: 0px;
}
.catchy-block li button:hover {background-color: rgb(230, 230 ,230)}
.catchy-block li button:active {padding-top: 3px}

/* Quotes Block */
.quotes-block .block-content {
    padding: 30px 0px 30px 0px;
}
.quotes-block .images {
    margin-bottom: 45px;
    height: 55px;
}
.quotes-block .images ul {
    display: flex;
    justify-content: center;
    list-style: none;
    padding: 0px;
}
.quotes-block .images li {
    display: inline-block;
    padding: 0px 30px 0px 0px;
}
.quotes-block .images li img {
    border-radius: 50%;
    height: 55px;
    width: 55px;
}
.quotes-block .images .active {
    border: 3px solid  rgba(0, 0, 255, 0.5);
}

.quotes-block .quote {
    text-align: center;
}
.quotes-block .quote p {
    font-family: 'Roboto', sans-serif;
    font-weight: 300;
    margin: 0px;
}
.quotes-block .quote p:first-child {
    margin-bottom: 10px;
    color: #727272;
    width: 40%;
    margin-left: auto;
    margin-right: auto;
}
